import React from "react";

export const Loading = () => {
  return (
    <div className="col-12 text-center mt-3 mb-3">
      <span className="fa fa-spinner fa-pulse fa-3x fa-fw text-primary"></span>
      <p className="mt-2">Loading ....</p>
    </div>
  );
};



