import React from "react";
import { Link } from "react-router-dom";

function Footer(props) {
  return (
    <div className="footer bg-warning">
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-4 offset-1 col-sm-2">
            <h5>Links</h5>
            <ul className="list-unstyled">
              <li>
                <Link to="/staffs">Nhân Viên</Link>
              </li>
              <li>
                <Link to="/departments">Phòng Ban</Link>
              </li>
              <li>
                <Link to="/payroll">Bảng Lương</Link>
              </li>
            </ul>
          </div>
          <div className="col-7 col-sm-5">
            <h5>Our Address</h5>
            <address>
              489 Hoàng Quốc Việt, Hà Nội <br />
              <i className="fa fa-phone fa-lg"></i>: +1234 5678
              <br />
              <i className="fa fa-fax fa-lg"></i>: +8765 4321
              <br />
              <i className="fa fa-envelope fa-lg"></i>:{" "}
              <a href="mailto:info@hrma.vn">info@hrma.vn</a>
            </address>
          </div>
          <div className="col-12 col-sm-4 align-self-center">
            <div className="text-center">
              <span className="p-1">
                <a
                  className="btn btn-social-icon btn-google"
                  href="http://google.com/+"
                >
                  <i className="fa fa-google-plus"></i>
                </a>
              </span>
              <span className="p-1">
                <a
                  className="btn btn-social-icon btn-facebook"
                  href="http://www.facebook.com/profile.php?id="
                >
                  <i className="fa fa-facebook"></i>
                </a>
              </span>
              <span className="p-1">
                <a
                  className="btn btn-social-icon btn-linkedin"
                  href="http://www.linkedin.com/in/"
                >
                  <i className="fa fa-linkedin"></i>
                </a>
              </span>
              <span className="p-1">
                <a
                  className="btn btn-social-icon btn-twitter"
                  href="http://twitter.com/"
                >
                  <i className="fa fa-twitter"></i>
                </a>
              </span>
              <span className="p-1">
                <a
                  className="btn btn-social-icon btn-google"
                  href="http://youtube.com/"
                >
                  <i className="fa fa-youtube"></i>
                </a>
              </span>
              <span className="p-1">
                <a className="btn btn-social-icon" href="mailto:">
                  <i className="fa fa-envelope-o"></i>
                </a>
              </span>
            </div>
          </div>
        </div>
        <div className="row justify-content-center">
          <div className="col-auto">
            <p>© Copyright 2022 Human Resource Management App</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Footer;



